.p-content-box {
  display: flex;
  flex-wrap: wrap;
}

.p-content-box__item {
  position: relative;
  flex: 0 0 50%;
  
  &:first-child {
    background-image: url(../../../WordPressTheme/assets/img/common/content1.jpg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;

  }

  &:nth-child(2){
    background-image: url(../../../WordPressTheme/assets/img/common/content2.jpg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
  }
  &:nth-child(3){
    background-image: url(../../../WordPressTheme/assets/img/common/content3.jpg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
  }
  &:nth-child(4){
    background-image: url(../../../WordPressTheme/assets/img/common/content4.jpg);
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
  }



  @include mq(tab) {
    flex: 0 0 25%;
 
  }
}


.p-content-box__link{
  display: inline-block;
  position: relative;
  width: 100%;
  height: 100%;
  padding: rem(81) 0 ;


  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .3s;
    }

    &:hover::after {
      background: rgba($color: #ffffff, $alpha: 0.6);

    }

    @include mq(tab) {
      padding: rem(157) 0 ;
    }
  
}

.p-content-box__link img {

}

.p-content-box__text {
  display: inline-block;
  font-size: rem(20);
  font-weight: 400;
  letter-spacing: 0.1em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;

  

  @include mq(tab) {
    font-size: rem(25);  
    letter-spacing: 0.08;
  }
  
  

}