.p-card {
  position: relative;
  display: inline-block;
  align-self:auto;

  
  &:hover {

    .p-card__body {
      background: #333333;

    }

    .p-card__title,
    .p-card__text,
    .p-card__date  {
      color: $text-color;
      
    }

   

    .p-card__img > img {
      transform: scale(1.2) translate(-40%, -40%);
    }

  }
}

.p-card__img {
  width: 100%;
  position: relative;
  overflow: hidden;
  
  }
  
  .p-card__img::before {
    padding-top: 66.56%; //画像の横 223/335 縦
    display: block;
    content: "";
    overflow: hidden;
  
  }
  
  .p-card__img img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
    transition: all .3s;
 
  }

.p-card__body {
   background: #fff;
   padding: rem(8) rem(15) rem(15) rem(23);
   color: #333;
   transition: all .3s;
   
   @include mq(tab) {
     padding: rem(4) rem(15) rem(15) rem(25);
     
   }
}

.p-card__title {
  font-size: rem(18);
  font-weight: 500;
  line-height: (35 / 18);
  letter-spacing: 0.0556em;
  text-align: left;  

  @include mq(tab) {
  }

}

.p-card__text {
  font-size: rem(14);
  font-weight: 400;
  letter-spacing: 0.0714em;
  line-height: (26 / 14);
  margin-top: rem(4);
  text-align: left;  

  @include mq(tab) {
    margin-top: rem(11);
  }


}

.p-card__bottom {
  margin-top: rem(8);
   display: flex;
   padding-right: rem(8);

   @include mq(tab) {
     margin-top: rem(12);
   }
}

.p-card__category {
 font-size: rem(14);
 padding: rem(2) rem(5);
 display: flex;
 justify-content: center;
 align-items: center;
 background:$category-color;
 letter-spacing: 0.0714em;

}

.p-card__date {
 font-size: rem(14);
 margin-left: auto;
 letter-spacing: 0.0714em;
}