.p-overview {

}

.p-overview__title {
  @include mq(tab) {
    letter-spacing: 0.0167em;    
  }


  &::before {
    content: "Overview";
    display: block;
    font-size: rem(50);
    font-weight: normal;
    position: absolute;
    top: rem(-49);
    left: 50%;
    transform: translateX(-50%);
    color: $title-color;
    opacity: 0.2;
    font-family: $font-family-sub;

    @include mq(tab) {
      left: rem(-228);
      font-size: rem(110);
      top: rem(-56);
      letter-spacing: 0.0182em;
      
    }
  }
}

.p-overview__content {
    margin-top: rem(46);
    background: $bg-sub-color;
    padding-bottom: rem(22);

    @include mq(tab) {
      margin-top: rem(34);
      background: linear-gradient(to left, transparent 30%,  $bg-sub-color 30%);
      width: 100%;
      padding-top: rem(80);
      padding-bottom: rem(80);
    }
  }

  .p-overview__inner {
    @include mq(tab) {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      margin-left: auto;
      margin-right: auto;
      padding-left: rem(4);
      width: 100%;
      max-width: rem(1110);
    }
  }

  .p-overview__img {
    width: 100%;
    position: relative;

    @include mq(tab) {
      // height: rem(510);
      display: flex;
      align-items: center;
      width: 100%;
      max-width: rem(557);
    }
    
  }

    .p-overview__img::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba($color: #111111, $alpha: 0.2);
    }
    
    
    .p-overview__img::before {
      padding-top: 69.6%; //画像の縦 261/ 375横
      display: block;
      content: "";
    
    }
    
    .p-overview__img img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
      vertical-align: bottom;
  @include mq(tab) {
    max-width: rem(557);
    
  }
    }
  
  .p-overview__text-box {
  width: 100%;
  margin-top: rem(21);
  padding-left: rem(21);
  padding-right: rem(21);
  
  @include mq(tab) {
    margin-top: rem(0);
    flex: 0 1 rem(574);
    padding-left: rem(0);
    padding-right: rem(32);
    padding-bottom: 5px;
    
  }

}

.p-overview__sub-title {
   font-size: rem(20);
   letter-spacing: 0.1em;
   text-align: center;
   
   @include mq(tab) {
     text-align: left; 
     font-size: rem(25);
     letter-spacing: 0.02em;
 }
}

.p-overview__text {
  margin-top: rem(24);
  line-height: (23 / 16);
  letter-spacing: 0.125em;
  
  @include mq(tab) {
    margin-top: rem(37);
   font-size: rem(16);  
   line-height: (35 / 16);
   letter-spacing: 0.0313em;
  }

}

.p-overview__btn {
  margin-top: rem(25);
  text-align: center;


  @include mq(tab) {
    margin-top: rem(48);  
    text-align: left;
    border: none;
   }
}

