.p-mv {
   width: 100%;
   height: 100vh;
   position: relative;
}

.p-mv__img {
  width: 100%;
  height: 100vh;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba($color: #111111, $alpha: 0.2);
  }
  
}

.p-mv__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}


.p-mv__content {
   width: 100%;
   text-align: center;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   z-index: 100;
}

.p-mv__title {
   font-size: rem(28);
   font-weight: 700;
   letter-spacing: 0.017em;
   line-height: 1;

   @include mq(tab) {
      font-size: rem(50);
      letter-spacing: 0.01em;
   }

}

.p-mv__suv-title {
   margin-top: rem(24);
   font-size: rem(20);
   font-weight: 700;
   letter-spacing: 0.025em;
   line-height: 1;

   @include mq(tab) {
      font-size: rem(25);
      letter-spacing: 0.02em;
   }

}


@keyframes zoom {
   from {
       transform: scale(1);
   }

   to {
       transform: scale(1.3);
   }
}

.p-mv-swiper {
   width: 100%;
   height: 100%;
   .swiper-wrapper {

       .swiper-slide {
           overflow: hidden;

           img {
             
           }
       }
       
       //Prevスライド
       .swiper-slide-prev,
       //Activeスライド
       .swiper-slide-active,
       //ループ時に複製される正面のスライド
       .swiper-slide-duplicate-active {

           img {
               animation-name: zoom;
               animation-duration: 8s;
               animation-fill-mode: forwards;
           }
       }
   }
}

